import React, { useEffect, useState } from 'react';

import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

// Import pages
import Explore from './pages/Explore';
import NotFound from './pages/NotFound';

// Import dark mode
import { useDarkMode } from './components/DarkMode';
import DarkModeToggle from 'react-dark-mode-toggle';

// Global css
import './App.css';

function App() {
  const [theme, toggleTheme, componentMounted] = useDarkMode();

  if (theme === 'dark') {
    document.documentElement.classList.add('mode-dark');
  } else {
    document.documentElement.classList.remove('mode-dark');
  }

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(false);
  }, [setLoading]);

  if (loading || !componentMounted) {
    return <div />;
  }

  // general link hover style
  const linkHover = `hover:text-blue-600 dark-hover:text-orange-500`;

  return (
    <Router>
      <div
        className="text-center w-screen pt-8 bg-gray-200 dark:bg-gray-700"
        style={{ marginBottom: '-1rem' }}>
        <DarkModeToggle
          onChange={toggleTheme}
          checked={theme === 'dark'}
          size={'3rem'}
          speed={5}
        />
      </div>
      <Switch>
        {/* Public Routes */}
        <Route exact path="/before">
          {<Explore />}
        </Route>

        {/* Catch-all Route */}
        <Route path="/">
          <NotFound />
        </Route>
      </Switch>
      <div className="text-center pb-12 text-gray-800 dark:text-gray-200">
        Made by{' '}
        <a
          className={`underline ${linkHover}`}
          href="https://kevinhu.io"
          target="_blank"
          rel="noopener noreferrer">
          Kevin Hu
        </a>
        <br />
        View{' '}
        <a
          className={`underline ${linkHover}`}
          href="https://github.com/kevinhu/before"
          target="_blank"
          rel="noopener noreferrer">
          source
        </a>{' '}
        on GitHub
      </div>
    </Router>
  );
}

export default App;
